<template>
  <div class="block2">
    <h1>a页面</h1>
    <p>params:{{$route.params}}</p>
    <p>query:{{$route.query}}</p>
    <p>q:{{q}}</p>
    <el-button @click="toB"></el-button>
    <template v-for="icon in icons">
      <br :key="icon + 'br'">
      {{icon}}
      <ant-icon :key="icon" :icon-class="icon"></ant-icon>
    </template>
  </div>
</template>

<script>
// import
import * as antIcons from '@ant-design/icons-vue/es'
export default {
  name: 'A',
  data () {
    return {
      // 定义的每个data 属性必须有注释
      // 注释规则： //
    }
  },
  props: {
    // q
    q: {
      default: '',
      type: String
    }
  },
  components: {},
  // beforeRouteEnter () {
  // },
  // beforeRouteUpdate () {
  // },
  // beforeRouteLeave () {
  // },
  beforeCreate () {
  },
  created () {
  },
  beforeMount () {
  },
  mounted () {
  },
  beforeUpdate () {
  },
  updated () {
  },
  beforeDestroy () {
  },
  destroyed () {
  },
  watch: {},
  computed: {
    icons () {
      return Object.keys(antIcons)
    }
  },
  directives: {},
  filter: {},
  methods: {
    // 方法调用不需要注释，方法定义需要注释
    // 方法注释规则 /** */
    toB () {
      this.$router.push('/Main/B')
    }
  }
}
</script>

<style scoped>
  .ant-icon{
    font-size: 20px;
  }
</style>



